import { instance } from "common/instance"
import { ApiTaskType, GetTasksResponse, UpdateTaskDomainModel, UpdateTaskModel } from "./tasksApi.types"
import { Respond } from "common/types/types"
import { baseApi } from "../../../app/baseApi"
/////////////////////
export const tasksApi = baseApi.injectEndpoints({
  endpoints: (builder) => ({
    getTasks: builder.query<GetTasksResponse, { todolistId: string; args: { count: number; page: number } }>({
      query: ({ todolistId, args }) => ({
        url: `/todo-lists/${todolistId}/tasks`,
        params: args,
        method: "GET",
      }),
      // providesTags:
      // providesTags: (result, error, todolistId) =>
      //   result
      //     ? [...result.items.map(({ id }) => ({ type: "Task", id }) as const), { type: "Task", id: todolistId }]
      //     : ["Task"],
      providesTags: (res, err, { todolistId }) => [{ type: "Task", id: todolistId }],
    }),
    createTask: builder.mutation<Respond<{ item: ApiTaskType }>, { title: string; todolistId: string }>({
      query: ({ title, todolistId }) => ({
        url: `/todo-lists/${todolistId}/tasks`,
        method: "POST",
        body: { title },
      }),
      // invalidatesTags: ["Task"],
      invalidatesTags: (result, error, { todolistId }) => [{ type: "Task", id: todolistId }],
    }),
    deleteTask: builder.mutation<Respond, { todolistId: string; taskId: string }>({
      query: ({ todolistId, taskId }) => ({
        url: `/todo-lists/${todolistId}/tasks/${taskId}`,
        method: "DELETE",
        body: {},
      }),
      // invalidatesTags: ["Task"],
      invalidatesTags: (result, error, { todolistId }) => [{ type: "Task", id: todolistId }],
    }),
    updateTask: builder.mutation<Respond<{ item: ApiTaskType }>, { task: ApiTaskType; updates: UpdateTaskDomainModel }>(
      {
        query: ({ task, updates }) => {
          const model: UpdateTaskModel = {
            title: task.title,
            description: task.description,
            completed: task.completed,
            status: task.status,
            priority: task.priority,
            startDate: task.startDate,
            deadline: task.deadline,
            ...updates, // Merge updates
          }

          return {
            url: `/todo-lists/${task.todoListId}/tasks/${task.id}`,
            method: "PUT",
            body: model,
          }
        },
        invalidatesTags: (result, error, { task }) => [{ type: "Task", id: task.todoListId }],
      },
    ),
  }),
})

export const { useGetTasksQuery, useCreateTaskMutation, useUpdateTaskMutation, useDeleteTaskMutation } = tasksApi

