// @flow
import * as React from "react"
import List from "@mui/material/List"
import { Task } from "./Task/Task"
import { TaskStatus } from "../../../../../common/enums/enums"
import { useGetTasksQuery } from "../../../api/tasksApi"
import { TodolistDomainType } from "../../../lib/types/types"
import { TasksPagination } from "../Todolist/TasksPagination/TasksPagination"
import { useState } from "react"

export const PAGE_SIZE = 4
type Props = {
  todo: TodolistDomainType
}
export const Tasks = ({ todo }: Props) => {
  const [page, setPage] = useState(1)

  const { data } = useGetTasksQuery({ todolistId: todo.id, args: { count: PAGE_SIZE, page } })
  // let totalCount = 0
  // if (data?.totalCount) {
  //   totalCount = data.totalCount
  // }
  const totalCount = data?.totalCount ?? 0

  const allTodolistTasks = data?.items
  let tasksForTodolist = allTodolistTasks

  if (todo.filter === "active") {
    tasksForTodolist = allTodolistTasks?.filter((task: any) => task.status === TaskStatus.New)
  }

  if (todo.filter === "completed") {
    tasksForTodolist = allTodolistTasks?.filter((task: any) => task.status === TaskStatus.Completed)
  }
  return (
    <>
      <div>
        {tasksForTodolist?.length === 0 ? (
          <p>Тасок нет</p>
        ) : (
          <List>
            {tasksForTodolist?.map((task: any) => {
              return <Task key={task.id} task={task} todo={todo} />
            })}
          </List>
        )}
      </div>
      {totalCount > PAGE_SIZE && <TasksPagination page={page} totalCount={totalCount} setPage={setPage} />}
    </>
  )
}
