import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react"
import { handleError } from "../common/utils/handleError"

export const baseApi = createApi({
  // keepUnusedDataFor: 60, //управляет Кеширование
  // refetchOnReconnect: true, // повторны запрос..соединение с интернетом после его потери
  // refetchOnFocus: true, //автоматически повторно запрашивать данные, когда окно приложения или вкладка браузера возвращаются в фокус
  reducerPath: "todolistsApi",
  tagTypes: ["Todolist", "Task"],
  baseQuery: async (args, api, extraOptions) => {
    const result = await fetchBaseQuery({
      baseUrl: process.env.REACT_APP_BASE_URL,
      prepareHeaders: (headers) => {
        headers.set("API-KEY", `${process.env.REACT_APP_API_KEY}`)
        headers.set("Authorization", `Bearer ${localStorage.getItem("sn-token")}`)
      },
    })(args, api, extraOptions)
    handleError(api, result)
    return result
  },
  endpoints: () => ({}),
})
