// @flow

import { createSlice } from "@reduxjs/toolkit"
import { todolistsApi } from "../api/todolistsApi"
export type ThemeMode = "dark" | "light"
export type RequestStatus = "idle" | "loading" | "succeeded" | "failed"

export const appSlice = createSlice({
  name: "app",
  initialState: {
    themeMode: "light" as ThemeMode,
    status: "idle" as RequestStatus,
    error: null as string | null,
    isLoggedIn: false,
    captcha: null as string | null,
  },
  reducers: (create) => ({
    setIsLoggedIn: create.reducer<{ isLoggedIn: boolean }>((state, action) => {
      state.isLoggedIn = action.payload.isLoggedIn
    }),
    setCaptchaUrl: create.reducer<{ captcha: string }>((state, action) => {
      state.captcha = action.payload.captcha
    }),
    setAppStatus: create.reducer<{ status: RequestStatus }>((state, action) => {
      state.status = action.payload.status
    }),
    setAppError: create.reducer<{ error: null | string }>((state, action) => {
      state.error = action.payload.error
    }),
    changeMode: create.reducer((state) => {
      state.themeMode = state.themeMode === "light" ? "dark" : "light"
    }),
  }),
  extraReducers: (builder) => {
    builder
      .addMatcher<RequestStatus>(
        (action) => action.type.endsWith("/pending"),
        (state, action) => {
          if (todolistsApi.endpoints.getTodolists.matchPending(action)) {
            return
          } else {
            state.status = "loading"
          }
        },
      )
      .addMatcher<RequestStatus>(
        (action) => action.type.endsWith("/fulfilled"),
        (state) => {
          state.status = "succeeded"
        },
      )
      .addMatcher<RequestStatus>(
        (action) => action.type.endsWith("/rejected"),
        (state) => {
          state.status = "failed"
        },
      )
  },

  selectors: {
    selectThemeMode: (state) => state.themeMode,
    selectErrorSnackbar: (state) => state.error,
    selectIsLoggedIn: (state) => state.isLoggedIn,
    selectCaptchaUrl: (state) => state.captcha,
  },
})
export const appReducer = appSlice.reducer
export const { setAppError, changeMode, setAppStatus, setIsLoggedIn, setCaptchaUrl } = appSlice.actions
export const { selectThemeMode, selectErrorSnackbar, selectIsLoggedIn, selectCaptchaUrl } = appSlice.selectors
