// @flow
import * as React from "react"
import { ChangeEvent } from "react"
import ListItem from "@mui/material/ListItem"
import { getListItemSx } from "../../Todolist/Todolist.styles"
import Checkbox from "@mui/material/Checkbox"
import IconButton from "@mui/material/IconButton"
import DeleteIcon from "@mui/icons-material/Delete"
import { EditableSpan } from "common/components"
import { ApiTaskType } from "../../../../api/tasksApi.types"
import { TaskStatus } from "../../../../../../common/enums/enums"
import { useDeleteTaskMutation, useUpdateTaskMutation } from "../../../../api/tasksApi"
import { RequestStatus } from "../../../../model/appSlice"
import { todolistsApi } from "../../../../api/todolistsApi"
import { useAppDispatch } from "../../../../../../common/hooks"
import { TodolistDomainType } from "../../../../lib/types/types"

type Props = {
  task: ApiTaskType
  todo: TodolistDomainType
}
export const Task = ({ todo, task }: Props) => {
  const dispatch = useAppDispatch()
  const [deleteTask] = useDeleteTaskMutation()
  const [updateTask] = useUpdateTaskMutation()
  const updateEntityStatus = (status: RequestStatus) => {
    dispatch(
      todolistsApi.util.updateQueryData("getTodolists", undefined, (state) => {
        const index = state.findIndex((tl) => tl.id === todo.id)
        if (index !== -1) {
          state[index].entityStatus = status
        }
      }),
    )
  }

  const removeTask = () => {
    updateEntityStatus("loading")
    deleteTask({ taskId: task.id, todolistId: todo.id })
      .unwrap()
      .then(() => {
        updateEntityStatus("succeeded")
      })
      .catch(() => {
        updateEntityStatus("failed")
      })
  }
  const changeTaskStatus = (e: ChangeEvent<HTMLInputElement>) => {
    let status = e.currentTarget.checked ? TaskStatus.Completed : TaskStatus.New
    updateTask({ task, updates: { status } })
  }
  const updateTaskHandler = (title: string) => {
    updateTask({ task, updates: { title } })
  }
  return (
    <ListItem key={task.id} sx={getListItemSx(task.status === TaskStatus.Completed ? true : false)}>
      <div>
        <Checkbox checked={task.status === TaskStatus.Completed ? true : false} onChange={changeTaskStatus} />
        <EditableSpan value={task.title} onChange={updateTaskHandler} />
      </div>
      <IconButton onClick={removeTask} disabled={todo.entityStatus === "loading"}>
        <DeleteIcon />
      </IconButton>
    </ListItem>
  )
}
