// @flow
import * as React from "react"
import { EditableSpan } from "common/components"
import IconButton from "@mui/material/IconButton"
import DeleteIcon from "@mui/icons-material/Delete"
import { todolistsApi, useDeleteTodolistMutation, useUpdateTodolistMutation } from "../../../api/todolistsApi"
import { useAppDispatch } from "../../../../../common/hooks"
import { RequestStatus } from "../../../model/appSlice"
import { TodolistDomainType } from "../../../lib/types/types"

type Props = {
  todo: TodolistDomainType
}

export const TodolistTitle = ({ todo }: Props) => {
  const todolistId = todo.id
  const dispatch = useAppDispatch()
  const [deleteTodolist] = useDeleteTodolistMutation()
  const [updateTodolist] = useUpdateTodolistMutation()

  const updateEntityStatus = (status: RequestStatus) => {
    dispatch(
      todolistsApi.util.updateQueryData("getTodolists", undefined, (state) => {
        const index = state.findIndex((tl) => tl.id === todolistId)
        if (index !== -1) {
          state[index].entityStatus = status
        }
      }),
    )
  }

  const removeTodolistHandler = () => {
    updateEntityStatus("loading")
    deleteTodolist(todolistId)
      .unwrap()
      .then(() => {
        updateEntityStatus("succeeded")
      })
      .catch(() => {
        updateEntityStatus("failed")
      })
  }

  const updateTodolistHandler = (title: string) => {
    updateTodolist({ todolistId, title })
  }
  return (
    <div className={"todolist-title-container"}>
      <h3>
        <EditableSpan value={todo.title} onChange={updateTodolistHandler} />
      </h3>
      <IconButton onClick={removeTodolistHandler} disabled={todo.entityStatus === "loading"}>
        <DeleteIcon />
      </IconButton>
    </div>
  )
}
